<template>
  <!---->
  <div v-if="tip.show">
    <!---->
    <div v-if="tip.netError" class="error">
      <span>,</span>
      <br>
      <span></span>
      <br>
      <br>
      <el-button type="primary" @click="refresh()" round icon="el-icon-refresh"></el-button>
    </div>

    <!---->
    <div v-if="tip.businessError" class="error">
      <span>,</span>
      <br>
      <span>{{tip.errorMessage}}</span>
    </div>

    <!--()-->
    <div v-if="tip.emptyAction" class="error">
      <span>,</span>
      <br>
      <span>^_^</span>
      <br>
      <br>
      <el-button type="primary" @click="writeAction()" round icon="el-icon-edit">?</el-button>
    </div>

    <!--()-->
    <div v-if="tip.emptyMessage" class="error">
      <span>,</span>
      <br>
      <span>^_^</span>
    </div>

    <!--()-->
    <div v-if="tip.emptyFocus" class="error">
      <span>,</span>
      <br>
      <span>^_^</span>
    </div>

    <!--()-->
    <div v-if="tip.emptyFans" class="error">
      <span>,</span>
      <br>
      <span>^_^</span>
    </div>
  </div>
</template>

<script>
import EventVue from "@/utils/EventVue.js";
export default {
  name: "Tip",
  data() {
    return {
      // tip: {
      //   //
      //   show: false,
      //   //
      //   netError: false,
      //   //
      //   businessError: false,
      //   //
      //   errorMessage: "",
      // }
    };
  },
  props: {
    tip: Object
  },
  methods: {
    refresh() {
      this.$emit("refresh");
    },
    writeAction() {
      this.$router.push("/write");
    }
  }
};
</script>

<style scoped>
.error {
  font-size: 40px;
  color: crimson;
  /* margin-top:  20px; */
}
</style>



