<template>
  <transition name="el-zoom-in-center">
    <el-card class="card" shadow="hover" v-show="showCard">
      <div slot="header">
        <span id="title"></span>
        <el-page-header @back="goBack"></el-page-header>
      </div>

      <div v-loading="loading" element-loading-text="..." class="main">
